@font-face {
  font-family: 'FiraSans';
  src: url("../../fonts/FiraSans-Regular.woff2") format("woff2"), url("../../fonts/FiraSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'FiraSansMedium';
  src: url("../../fonts/FiraSans-Medium.woff2") format("woff2"), url("../../fonts/FiraSans-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'FiraSansBold';
  src: url("../../fonts/FiraSans-Bold.woff2") format("woff2"), url("../../fonts/FiraSans-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Montserrat';
  src: url("../../fonts/Montserrat-Bold.woff2") format("woff2"), url("../../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Raleway';
  src: url("../../fonts/Raleway-Black.woff2") format("woff2"), url("../../fonts/Raleway-Black.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'RalewayBold';
  src: url("../../fonts/Raleway-ExtraBold.woff2") format("woff2"), url("../../fonts/Raleway-ExtraBold.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

html {
  font-size: 15px;
  height: 100%; }

body {
  background-color: #E5E5E5;
  color: #484848;
  font-family: "FiraSans", Arial, sans-serif;
  height: 100%;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed; }

*,
*::before,
*::after {
  box-sizing: border-box; }

ul[class],
ol[class] {
  list-style: none;
  padding: 0; }

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0; }

img {
  max-width: 100%;
  display: block; }

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important; } }

audio,
canvas,
iframe,
img,
svg,
video,
textarea {
  vertical-align: middle; }

textarea {
  resize: none; }

section {
  position: relative; }

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%; }

.container {
  margin: 0 auto;
  padding: 0 110px;
  position: relative; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  background-image: url("../img/girl1x.png"), url("../img/izrail_title1x.png"), url("../img/hero_bg1x.png");
  background-position: top, center 80px, top;
  background-size: 100%, 75%, 100%;
  background-repeat: no-repeat, no-repeat, no-repeat;
  height: 100vh; }
  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .header {
      background-image: url("../img/girl2x.png"), url("../img/izrail_title2x.png"), url("../img/hero_bg2x.png"); } }
  .header__nav {
    align-items: center;
    display: flex;
    justify-content: space-between;
    padding-bottom: 41%;
    padding-top: 32px; }
  .header__phone {
    color: #484848;
    font-family: "FiraSansMedium", Arial, sans-serif;
    line-height: 18px;
    text-decoration: none; }
    @media screen and (max-width: 767px) {
      .header__phone {
        order: 2; } }
  .header__callback {
    border-bottom: 1px dashed #484848;
    font-family: "FiraSansMedium", Arial, sans-serif;
    line-height: 18px;
    text-transform: uppercase; }
    @media screen and (max-width: 767px) {
      .header__callback {
        order: 2; } }

.hero__text {
  color: #ffffff;
  font-family: "FiraSansBold", Arial, sans-serif;
  font-size: 24px;
  line-height: 29px;
  margin: 0 auto 32px auto;
  text-align: center;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
  width: 30%; }

.hero__mouse-scroll {
  display: flex;
  justify-content: center;
  position: relative; }
  .hero__mouse-scroll::after {
    background-color: #ffffff;
    bottom: -56px;
    content: "";
    height: 56px;
    position: absolute;
    width: 1px; }

.hero__icon--big-circle {
  animation: around 6s infinite linear;
  fill: transparent; }

.hero__icon--small-circle {
  fill: transparent;
  position: absolute;
  top: 11px; }

.hero__icon--mouse {
  animation: mouse 2s infinite linear;
  fill: white;
  position: absolute;
  top: 24px; }

@keyframes mouse {
  0% {
    transform: translateY(0); }
  25% {
    transform: translateY(3px); }
  50% {
    transform: translateY(0); }
  75% {
    transform: translateY(-3px); }
  100% {
    transform: translateY(0); } }

@keyframes around {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
